* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;

    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent
}
html {
    background: #f7f8f9;
}
body {
    font-family: Nunito, Helvetica Neue, Helvetica, PingFang SC, Microsoft YaHei, 微软雅黑, Arial, sans-serif;
    font-size: 100%;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}
li {
    line-height: 1.5;
}
img {
    image-rendering: -webkit-optimize-contrast;
}
img[lazy=loading] {
    opacity: .1;
}
img[lazy=loaded] {
    opacity: 1;
}
.page-wrapper {
    padding: 1rem;
}
.t-center {
    text-align: center;
}
img,
.shadow,
.submit {
    -webkit-transition: all .25s ease;
    transition: all .25s ease;
}
.shadow {
    -webkit-box-shadow: 0 1px 10px rgba(36, 37, 38, .08);
    box-shadow: 0 1px 10px rgba(36, 37, 38, .08);
}
.shadow:hover {
    -webkit-box-shadow: 0 5px 10px rgba(36, 37, 38, .1);
    box-shadow: 0 5px 10px rgba(36, 37, 38, .1);
}
.menu,
.menu-cursor,
.logo,
.header,
.app-wrapper,
.os-card,
.get-kext a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.card,
.app,
.os-card,
.theme-card,
.kext-table {
    background: white;
    border-radius: .5rem;
}

/* sidebar */
.sidebar {
    top: 0;
    z-index: 999;
    position: fixed;
    background: white;
}
.logo {
    z-index: 1000;
    position: fixed;
    top: 0;
}
.logo img {
    margin: 1.5rem 0 0 1rem;
    width: 2rem;
    height: 2rem
}
.sidebar-wapper {
    -webkit-transition: all .25s ease;
    transition: all .25s ease;
}
.menu {
    padding: 0 1.25rem;
}
.menu,
.logo {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #929ca7;
    font-size: .8rem;
    font-weight: 700
}
.menu svg {
    width: 1.5rem;
    fill: #929ca7;
    margin-right: 1.25rem
}
.menu:hover {
 border-left: 3px #43c345 solid;
 padding: 0 calc(1.25rem - 3px);
}
.menu-cursor {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: .5rem 0
}
.menu:hover svg {
    fill: #43c345
}
.menu:hover span {
    color: #43c345
}
.menu:hover,
.router-link-exact-active {
    border-left: 3px #43c345 solid;
    padding: 0 calc(1.25rem - 3px);
}
.router-link-exact-active svg {
    fill: #43c345
}
.router-link-exact-active span {
    color: #43c345
}

/* header submit */
.header {
    width: 100%;
    padding: 1rem 1rem 2rem 0;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.title {
    font-weight: 700;
    color: #131727;
    text-align: left;
    font-size: 2rem;
}
.submit {
    margin-left: 2rem;
    padding: .75rem 3rem;
    background: #57cb5b;
    -webkit-columns: white;
    color: white;
    font-weight: 700;
    border-radius: 2px;
    -webkit-box-shadow: 0 2px 10px rgba(87, 203, 91, .5);
    box-shadow: 0 2px 10px rgba(87, 203, 91, .5);
}
.submit:hover {
    -webkit-box-shadow: 3px 5px 10px rgba(87, 203, 91, .5);
    box-shadow: 3px 5px 10px rgba(87, 203, 91, .5);
}
.card-bottom {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.author {
    font-weight: 700;
    font-size: 1rem;
    line-height: 2rem;
    color: #5d648e
}
.author span {
    display: inline-block;
    background: #b6d2f6;
    color: #4d8de2;
    border-radius: 10rem;
    height: 2rem;
    width: 2rem;
    margin-right: .5rem;
    text-align: center;
}

/* Responsive */

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
.logo img {
        margin-top: .5rem
}
.menu span {
        display: none;
}
.menu svg {
        margin-right: 0;
}
.sidebar-wapper {
        padding: .25rem 1rem!important;
}
.page-wrapper {
        padding: 4rem 1rem;
}
.header {
        padding: 1rem 0;
        height: 5rem
}
.title {
        font-size: 1.25rem;
}
.submit {
        font-size: 1rem;
        padding: .5rem 2rem;
}
.author {
        font-size: 12px;
        line-height: 16px
}
.author span {
        padding: 5px 4px 4px;
        height: 24px;
        width: 24px
}
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
.page-wrapper {
        padding: 5rem 1rem 1rem 2rem;
}
.wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
        flex-flow: row wrap;
}
}

/*  768px and down */
@media only screen and (max-width: 767px) {
.logo {
        display: none;
}
.sidebar {
        width: 100%;
        border-bottom: #eef1f2 1px solid;
}
.sidebar-wapper {
        padding: .25rem 2rem;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
}
.menu:hover,
    .router-link-exact-active {
        border-left: none;
        padding: 0 .25rem;
}
.menu {
        padding: 0 .25rem;
}
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
.sidebar {
        left: 0;
        height: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        border-right: #eef1f2 1px solid;
}
.sidebar-wapper {
        width: 4rem;
        overflow: hidden;
}
.page-wrapper {
        padding: 2rem 2rem 2rem 7rem;
}
.menu-cursor {
        padding: 1rem 0
}
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
.sidebar-wapper:hover {
        width: 8.3rem
}
.page-wrapper {
        padding: 2rem 4rem 2rem 9rem;
}
