.app-wrapper {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    text-align: center;
}
.app-wrapper,
.os-wrapper {
    margin-top: 2rem;
    text-align: center;
}
.app,
.os-card {
    -webkit-box-flex: 0; 
    -ms-flex: 0 0 calc(50% - 1rem); 
    flex: 0 0 calc(50% - 1rem);
    margin: 1rem 1rem 0 0;
    padding: 1rem;
}
/* free app*/
.app-title {
    font-size: 3rem;
    font-weight: 900;
}
.app-logo {
    margin: 0 auto;
    padding: 1rem 0;
}
.app-logo img {
    width: 8rem;
}
.app-icon {
    padding: 1rem 0;
    width: 50%;
}
.app-name {
    font-size: 1.2rem;

    padding-bottom: 1rem;

    color: #333435;
}
.app-des {
    color: #333;
    font-size: .9rem;
    line-height: 1.4;
}
/* OS */
.os-wrapper {
    margin-top: 0!important
}
.os-card {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.os-icon {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
}
.os-icon img {
    width: 70%;
}
.os-detail {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
}
.os-name {
    font-size: 2rem;

    color: #191b34;
}
.os-detail li {
    font-size: 0.9rem;
    color: #949ea9
}
.os-detail li:last-child {
    color: #5d648e
}
/* themes */
.theme-card {
    margin-top: 1rem;
    padding: .5rem;
    color: #191b34
}
.theme-card img {
    width: 100%;
}
/* kext */
.kext-wrapper,
.kext-table,
.kext-sort {
    width: 100%;
}
.kext-table {
    margin-top: 1rem;
    border-collapse: collapse;
    border-spacing: 0;
    overflow: auto;
    color: #7c8c96;
}
.kext-sort {
    background: white;
    color: #223464;
    font-size: 1.5rem;
    padding: 1rem 0 1rem 2rem
}
.kext-table tr:hover {
    background: #f6f8fa
}
.kext-table td {
    padding: 1rem;
    border: 1px solid #f5f5f6;
}
.kext-des {
    min-width: 20rem;
}
.get-kext {
    padding: 0 2rem!important
}
.get-kext a {
    vertical-align: center
}
.get-kext img {
    height: 1.5rem;
    width: 1.5rem
}
/* Responsive */
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
.app-page {
        padding: 3rem .5rem;
}
.app {
        margin: .5rem
}
.app-title {
        font-size: 2rem
}
.app-logo img {
        width: 6rem;
}
.app-icon {
        padding: .5rem 0;
        width: 60%;
}
.app-name {
        font-size: 1rem;
        padding-bottom: .5rem
}
.app-des {
        font-size: .75rem
}
.theme-card {
        display: block;
}
.os-card {
        margin: 1rem 0 0;
}
.os-name {
        font-size: 1.5rem;
}
.app-wrapper, 
    .os-wrapper {
        margin-top: 1rem
}
.kext-wrapper {
        overflow-x: scroll;
}
.kext-sort {
        padding: 1rem 0 1rem 1rem
}
}
/* Extra small devices (phones, 600px and up) */
@media only screen and (min-width: 600px) {
.theme-card {
        margin-right: 1rem;
        -webkit-box-flex: 0;
        -ms-flex: 0 0 calc(50% - 1rem);
        flex: 0 0 calc(50% - 1rem);
}
.os-name,
    .app-name {
        font-size: 1.2rem
}
.os-icon img {
        width: 90%
}
.kext-table {
        margin-right: 1rem;
}
.kext-table td {
        padding: 1rem 2rem;
}
.app-name {
        padding-bottom: 1rem;
}
.app-des {
        font-size: .9rem;
        line-height: 1.4;
}
}
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
.app,
    .os-card,
    .theme-card {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 calc(33.3333% - 1rem);
        flex: 0 0 calc(33.3333% - 1rem);
}
}
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
.app,
    .os-card { 
        -webkit-box-flex: 0; 
        -ms-flex: 0 0 calc(25% - 1rem); 
        flex: 0 0 calc(25% - 1rem);
}
.os-icon img {
        width: 100%
}
.app-icon {
        width: 45%;
}
.app-name {
        font-size: 1.4rem;

        padding-bottom: 1rem;
}
.app-des {
        font-size: 1rem;
        line-height: 1.4;
}
}
/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
.os-name {
        font-size: 1.5rem
}
.app-icon {
        width: 40%;
}
}
@media only screen and (min-width: 1500px) {
.theme-card {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 calc(25% - 1rem);
        flex: 0 0 calc(25% - 1rem);
}
.app,
    .os-card { 
        -webkit-box-flex: 0; 
        -ms-flex: 0 0 calc(20% - 1rem); 
        flex: 0 0 calc(20% - 1rem);
}
